@mixin button($background-color: #F61D54,$color: #f6501d ) {
    padding: 1rem;
    font-weight: 600;
    background-color: $background-color;
    border: solid  $background-color 2px;
    color: white;
    border-radius: 5px;
    transition: 1s;
    cursor: pointer;
    &:hover{
        background-color: bisque;
        color:rgb(54, 54, 54);
        border: bisque solid 2px;
    }
  }
  @mixin slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
  
    &_name {
      margin: 0%;
      color: white;
    }
  
    &_figure {
      flex-grow: 1;
      display: unset;
      position: relative;
      display: inline-block;
      height: 300px;
  
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 90%;
        background: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
        pointer-events: none;
      }
    }
  
    &_images {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  
    &_avatar {
      img {
        height: 10px;
        width: 10px;
        border-radius: 10px;
      }
    }
  
    .slick-dots li.slick-active button:before {
      opacity: 0.75;
      color: bisque;
    }
  
    .slick-dots li button:before {
      font-family: 'slick';
      font-size: 6px;
      line-height: 20px;
      position: absolute;
      top: 0;
      left: 0;
      width: 5px;
      height: 5px;
      content: '•';
      font-size: 8px;
      text-align: center;
      opacity: 0.25;
      color: white;
      -webkit-font-smoothing: antialiased;
    }
  
    .slick-dots {
      position: absolute;
      bottom: 19px;
      display: block;
      width: 100%;
      padding: 0;
      margin: 0;
      list-style: none;
      text-align: center;
    }
  }
  